
@import "meyer";

@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800|Karla:400,700&display=swap');


html {
    min-height: 500px;
}

body {
    font-family: 'Karla', sans-serif;
    color: #333;
    font-weight: 400;
}

#home {
    height: 100%;
    body {
        height: 100%;
        background: url(images/header.jpg);
        background-size: cover;
        display: flex;
        flex-direction: column;
        header {
          background: none;
          box-shadow: none;
        }
        footer {
            width: 100%;
        }
    }

}

.not-under-400 {
    @media (max-width: 400px) {
        display: none;
    }
}

.not-over-400 {
    @media (min-width: 400px) {
        display: none;
    }
}

section {
    max-width: 1200px;
    margin: auto;
}


p {
    line-height: 1.5;
    font-size: 1.2em;
}

h2 {
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 2em;
    letter-spacing: 2px;
    padding: 3em 0 2em;
    line-height: 1.5;
    color: #243;
}


h3 {
    line-height: 1.5;
    font-size: 2.2em;
    padding-bottom: 1em;
    //color: #243;
    a {
      text-decoration: none;
      color: inherit;
    }
  }
  
  h4 {
      text-transform: uppercase;
      padding:2em 0 0.5em;
      font-weight: 700;
      font-size: 1.2em;
  }
  


header {
    background: url(images/header.jpg);
    background-size: cover;
    background-position: top;
    box-shadow: 0 0 10px #000;
    #navbox {
      max-width: 1200px;
      margin: 0 auto;
      img {
        filter: drop-shadow(0 0 10px #000) brightness(10) contrast(0.7);
        margin: 1.5em;
        width: calc(100% - 3em);
      }
      @media (min-width: 900px) {
        padding: 1em 0;
      }
      @media (max-width: 1200px) {
        background: rgba(0, 0, 0, 0.2);
      }
    }
  
    
/*    #logo-full {
      @media (min-width: 400px) {
        display: none;
      }    
    }  */
    #logo-text {
      @media (max-width: 900px) {
        max-width: 300px;
        display: block !important;
        margin: 0 auto 2em !important;
        padding-top: 2em;
      }
      @media (min-width: 400px) {
        display: inline;
      }
      @media (min-width: 900px) {
        width: calc(30% - 3em);
        max-width: 220px;
        display: inline-block;
      }    
    }
    nav {
      margin: 1em 0 0;
      padding: 2em 0;
      border-top: 1px solid rgba(255, 255, 255, 0.1);
      display: flex;
      justify-content: space-evenly;
      @media (min-width: 900px) {
        display: inline-flex;
        float: right;
        width: 70%;
        max-width: 700px;
        margin: 2.5em 0;
        border-top: none;
      }    
      a {
        color: #CCC;
        text-decoration: none;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-size: 1em;
        @media (min-width: 400px) {
            font-size: 1.2em;
        }
        &:hover {
          color: #EEE;
        }
        &.activelink {
          font-weight: 700;
          display: none;
          @media (min-width: 500px) {
            display: inline;
          }
          
        }
      }
    }
    h1 {
      border-top: 1px solid rgba(255, 255, 255, 0.1);
      padding: 1em 0 1.5em;
      color: #EEE;
      text-align: center;
      font-size: 2em;
      font-weight: 700;
      line-height: 1.5;
      text-transform: uppercase;
      letter-spacing: 2px;
      @media (min-width: 400px) {
        padding: 2em 0 2.5em;
        font-size: 3em;
      }    
    }
  }


  #homesection {
    h1 {
      padding: 1em 0;
      color: #EEE;
      text-align: center;
      font-size: 2em;
      font-weight: 700;
      line-height: 1.5;
      text-transform: uppercase;
      letter-spacing: 2px;
      @media (min-width: 400px) {
        font-size: 3em;
      }    
    }
  }


  #aboutsection {
      padding: 1em 1em 4em;
      h3 {
        margin-top: 2em;
        padding-bottom: 0;
      }
      h4 {
          line-height: 1.5;
      }
      p {
          margin: 1em 0;
      }
  }

  #teamsection {
      padding: 2em 1em;
      .team-member {
          width: 100%;
          padding: 3em 0;
          margin: 1em 0 0;
          border-bottom: 1px dashed #CCC;
          display: flex;
          flex-direction: column;
          @media (min-width: 800px) {
            flex-direction: row;
          }
          &:last-child {
              border-bottom: none;
          }
          img {
              width: 100%;
              height: 100%;
              border-radius: 300px;
              max-width: 300px;
              box-shadow: 0 0 10px #CCC;
              display: block;
              margin: 0 auto;
              @media (min-width: 800px) {
                margin-top: 2em;
              }
          }    
      }
      .team-profile {
          padding: 1em 0;
          @media (min-width: 800px) {
            padding-left: 2em;
          }
          @media (min-width: 1000px) {
            padding-left: 3em;
          }          
          @media (min-width: 1200px) {
            padding-left: 4em;
          }
          h3 {
              font-size: 2em;
              text-align: center;
              padding-bottom: 0;
              @media (min-width: 800px) {
                text-align: left;
              }
          }
          h4 {
              text-align: center;
              padding: 0.5em 0;
              font-size: 1em;
              @media (min-width: 800px) {
                text-align: left;
              }
          }
          p {
              text-align: center;
              font-size: 1em;
              padding: 0.5em 0;
              @media (min-width: 800px) {
                text-align: left;
              }
          }
      }
    .team-boxes {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        .person-box {
            box-shadow: 0 0 10px #CCC;
            position: relative;
            width: 350px;
            margin: 2em;
            img {
                width: 100%;
            }
            h3 {
                text-align: center;
                padding: 0.5em 0 0;
                text-transform: uppercase;
                font-size: 1.6em;
            }
            h4 {
                text-align: center;
                padding: 1em 0;
            }
            a {
                  opacity: 0;
                  display: none;
            }
            &:hover {
                a {
                      display: block;
                      opacity: 1;
                      text-decoration: none;
                      position: absolute;
                      top: 0;
                      left: 0;
                      width: 100%;
                      padding: 3.5em 0;
                      background: rgba(0,0,0,0.7);
                      text-align: center;
                      color: #FFF;
                      font-size:  1.6em;
                      text-transform: uppercase;
                      font-weight: 600;
                }
            }
        }
    }

}

  #projectssection {
      h3 {
          text-align: center;
          font-size: 2em;
          padding: 3em 0 2em;
          line-height: 1.2;
      }
      .projects-boxes {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          justify-content: center;
          .project-box {
              box-shadow: 0 0 10px #CCC;
              position: relative;
              width: 300px;
              margin: 2em;
              img {
                  width: 100%;
              }
              h4 {
                  text-align: center;
                  padding: 1em 0;
              }
              .popup-link {
                    opacity: 0;
                    display: block;
                    text-decoration: none;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    padding: 3.5em 0;
                    background: rgba(0,0,0,0.7);
                    text-align: center;
                    color: #CCC;
                    font-size: 1.6em;
                    text-transform: uppercase;
                    font-weight: 600;         
                    transition: opacity 500ms;           
              }
              &:hover {
                  .popup-link {
                        opacity: 1;
                  }
              }
          }
      }
      #platform-box {
          .project-box {
              margin: 0 2em 5em;
              img {
                    padding: 1em;
                    box-shadow: 0 0 20px #CCC inset;
                    width: calc(100% - 2em);
              }
          }
      }
  }

  #newssection {
      padding: 1em 1em 2em;
      .blogpost {
          padding: 0.5em 0 1em;
          border-top: 1px dashed #CCC;
          margin-top: 1em;
          &:first-child {
              border-top: none;
          }
          @media (min-width: 800px) {
              padding: 3em 1em 2em;
              min-height: 200px;          
          }
          img {
              display: none;
              width: 300px;
              height: 200px;
              box-shadow: 0 0 10px #CCC;
              @media (min-width: 800px) {
                  display: block;
                  float: left;
                  margin-right: 2em;
              }
          }
          h4 {
              font-weight: 400;
              letter-spacing: 1px;
          }
          h3 {
              padding-bottom: 0.5em;
              font-size: 2em;
              line-height: 1.2;
          }
          p {
              a {
                  text-decoration: none;
                  font-variant: small-caps;
                  text-transform: lowercase;
                  color: inherit;
                  font-weight: 600;
              }
          }
      }
  }

  #contactsection {
    display: flex;
    flex-direction: column;
    @media (min-width: 800px) {
      flex-direction: row;
      justify-content: space-evenly;
    }
    #contactinfo {
      padding: 1em 1em 2em;
      margin: 0 auto;    
      h3 {
            font-size: 1.2em;
      }
      @media (min-width: 800px) {
          padding: 5em 1em;
      }
    }
    #contactform {
      padding: 1em;
      background: #eee;
      h3 {
          padding: 0.5em;
          max-width: 400px;
          margin: auto;
          font-size: 1.2em;
          font-weight: 700;
      }
      
      input, textarea {
        width: calc(100% - 1em);
        max-width: 400px;
        margin: 1em auto;
        display: block;
        padding: 0.5em;
        font-family: inherit
      }
      textarea {
        height: 5em;
        background: #fff;
      }
      button {
        margin: 2em auto;
        display: block;
        font-size: 1em;
        padding: 0.5em 2em;
      }
      @media (min-width: 800px) {
        z-index: -1;
        width: 40%;
        padding: 5em 2em;
      }
    }
  }
  
footer {
    background: rgba(0, 0, 0, 0.8);
    background-size: cover;
    background-position: bottom;
    box-shadow: 0 0 5px #000;
    //filter: brightness(0.7);
    padding: 2em 0;
    div {
        text-align: center;
        color: #CCC;
        letter-spacing: 1px;
        line-height: 1.5;
        a {
            color: #CCC;
            line-height: 2em;
            text-transform: uppercase;
            font-weight: 700;
        }
    }
}




.overlay {
    z-index: 1;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 500ms;
    visibility: hidden;
    opacity: 0;
    &:target {
      visibility: visible;
      opacity: 1;
    }
  }

  .popup {
    margin: 70px auto;
    padding: 1em;
    background: #fff;
    border-radius: 5px;
    width: calc(100% - 4em);
    max-width: 800px;
    position: relative;
    transition: all 500ms;
    display: flow-root;
    h4 {
      padding-top: 0.4em !important;
      text-align: left !important;
      border-bottom: 1px dashed #CCC;
    }
    img {
      display: block;
      max-width: 300px;
      margin: 2em auto 1em;
      box-shadow: 0 0 10px #CCC;
      @media (min-width: 600px) {
        float: right;
        margin-left: 1em;

      }
      &:after {
        content: '';
        clear: both;
      }
    }

    p {
      margin-top: 1.3em;
    }
  }
  .popup .close {
    position: absolute;
    top: 20px;
    right: 30px;
    transition: all 200ms;
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    color: #333;
  }
  .popup .content {
    max-height: 30%;
    overflow: auto;
  }